/*  */

body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,

/* structural elements  */

dl,
dt,
dd,
ul,
ol,
li,

/* list elements  */

pre,

/* text formatting elements  */

fieldset,
lengend,
button,
input,
textarea,

/* form elements  */

th,
td {
    /* table elements  */
    margin: 0;
    padding: 0;
}


/*  */

body,
button,
input,
select,
textarea {
    /* for ie */
    /*font: 12px/1 Tahoma, Helvetica, Arial, "", sans-serif;*/
    font: 12px/1 Tahoma, Helvetica, Arial, "\5b8b\4f53", sans-serif;
    /*  ascii  */
}

h1 {
    font-size: 18px;
    /* 18px / 12px = 1.5 */
}

h2 {
    font-size: 16px;
}

h3 {
    font-size: 14px;
}

h4,
h5,
h6 {
    font-size: 100%;
}

address,
cite,
dfn,
em,
var {
    font-style: normal;
}


/*  */

code,
kbd,
pre,
samp,
tt {
    font-family: "Courier New", Courier, monospace;
}


/*  */

small {
    font-size: 12px;
}


/*  12px  small  */


/*  */

ul,
ol {
    list-style: none;
}


/*  */

a {
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

abbr[title],
acronym[title] {
    /* 1.ie6  abbr; 2.ie6  */
    border-bottom: 1px dotted;
    cursor: help;
}

q:before,
q:after {
    content: '';
}


/*  */

legend {
    color: #000;
}


/* for ie6 */

fieldset,
img {
    border: none;
}


/* img  img  */


/* optgroup  */

button,
input,
select,
textarea {
    font-size: 100%;
    /*  ie  */
}


/*  */

table {
    border-collapse: collapse;
    border-spacing: 0;
}


/*  hr */

hr {
    border: none;
    height: 1px;
}


/* ie */

html {
    overflow-y: scroll;
}


